import React from 'react'
import { makeStyles } from "@material-ui/core/styles";
import Style from './SideBarStyle';
import SideBarMenuItems from "../../Constant"
import StrokeStyle from '../StrokeStyle'
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import inputMode from '../InputMode.js'
import ShapeLibrary from '../ShapeLibrary'
import AppInput from '../AppInput'

const useStyles = makeStyles(Style);

const isSubMenus = (sidebar) =>{
    if(Object.keys(sidebar).length > 0 && "subMenus" in sidebar)
    {
        return true;
    }
    return false;
}
const SideBarMenu = () => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState();
    const [subMenuIcons, setSubMenuIcons] = React.useState();
    const [selectedItemId, setSelectedItemId] = React.useState(inputMode.mode);
    const [selectedRestMenuItem,setSelectedRestMenuItem] = React.useState();
    const classes = useStyles();
    
    const handleClick = (event, subMenus, newPlacement,id) => {
        if (id == 6) {
            // Erase All
            inputMode.setMode(id);
            return;
        }
        setAnchorEl(event.currentTarget);
        //setOpen((prev) => placement !== newPlacement || !prev);
        var willOpen = false;
        if(id === selectedItemId) {
            setOpen(!open);
            willOpen = !open;
        }else {
            setOpen(true);
            willOpen = true;
        }

        if (willOpen && (id == 3 || id == 4 || id == 7)) {
            AppInput.ignoreNextClick(true);
        } else {
            AppInput.ignoreNextClick(false);
        }

        setPlacement(newPlacement);
        setSubMenuIcons(subMenus)
        setSelectedItemId(id)
        let selectedItem = (id - 1);
        setSelectedRestMenuItem(selectedItem)
        inputMode.setMode(id);
    };
    
    const getIconAndName = (sidebar, classes) => {
        const {icon, name} = sidebar;
        const {menuItem} = classes; 
        return (
            <div>
                <div className={menuItem + ' ' + 'sideMenuIcons'}>
                    {icon}
                </div>
                <div className={menuItem +' '+ 'menuItems'}>{name}</div>
            </div>
        )
    }
    return (
        <ClickAwayListener onClickAway={() => setOpen(false)}>
        <div>
           { (selectedItemId === 3 || selectedItemId === 4) && <StrokeStyle
                open={open}
                anchorEl={anchorEl}
                placement={placement}
                subMenuIcons ={subMenuIcons}
                setOpenState={()=>setOpen(!open)}
                close={() => console.log('close')}
                selectedItemId={selectedItemId}
            /> }
            { selectedItemId === 7 && <ShapeLibrary
                open={open}
                anchorEl={anchorEl}
                placement="right-end"
                subMenuIcons ={subMenuIcons}
            /> }
            
            <div className={`${classes.sideBar} side_menus`}>
                {SideBarMenuItems.map((sidebar, index) => {
                    if (isSubMenus(sidebar)) {
                        return (
                            <div className={sidebar.id === selectedItemId ? classes.sideBarMenu +' '+ 'sideBarMenu active' : classes.sideBarMenu +' '+ 'sideBarMenu'} key={index} onClick={(event)=>handleClick(event, sidebar.subMenus,'right',sidebar.id)} >
                                {getIconAndName(sidebar, classes)}
                            </div>
                        )
                    }
                    return (
                        <div className={index != 5 && index === selectedRestMenuItem ? classes.sideBarMenu +' '+ 'sideBarMenu active' : classes.sideBarMenu +' '+ 'sideBarMenu'} key={index} onClick={(event)=>handleClick(event, sidebar.subMenus,'right',sidebar.id)}>
                           {getIconAndName(sidebar, classes)}
                        </div>
                    )
                })}

            </div>
        </div>
        </ClickAwayListener>
        
    )
}
export default SideBarMenu