import React from 'react'
import Popper from '@material-ui/core/Popper';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import canvas from './Canvas'
import Draggable from 'react-draggable';
import Config from './Config'

const ShapeLibrary = (props) => {
    const onDragStop = (event, subMenu, data) => {
        var categ = subMenu.props.category;
        var num = subMenu.props.num;
        var x = event.pageX;
        var y = event.pageY;
        if (x == null || y == null || typeof x == "undefined" || typeof y == "undefined") {
            if (event.changedTouches && event.changedTouches.length > 0) {
                var touch  = event.changedTouches[0];
                x = touch.pageX;
                y = touch.pageY;
            }
        }
        if (x == null || y == null || typeof x == "undefined" || typeof y == "undefined") {
            return;
        }
        
        var popUpRect = popperRef.current.getBoundingClientRect()
        if (y>= popUpRect.top && y<= popUpRect.bottom && x >= popUpRect.left && x <= popUpRect.right) {
            var tp = document.getElementById('transparentPanel');
            var rect = tp.getBoundingClientRect();
            x = (rect.left + rect.right) / 2;
            y = (rect.top + rect.bottom) / 2;
        }
        x = canvas.maprx(x);
        y = canvas.mapry(y);
        window.Module.libraryStore().addObjectToCanvas(categ, num, x, y);
        canvas.draw();
    }

    const getSubIcons = (subMenuIcons) =>{
        return subMenuIcons?.map((subMenu,index)=>{
            return (
                <Draggable key={index} position={{x: 0, y: 0}} onStop={(event, data) => onDragStop(event, subMenu, data)}>
                  <div style={{display:"inline-block"}}>
                    {subMenu}
                  </div>
                </Draggable>
            )
        })
    }

    var {open, anchorEl, placement, subMenuIcons} = props;
    if (window.matchMedia(Config.mediaQuery).matches) {
        placement = "top";
    }
    var popperRef = React.createRef();
    return (
        <div>
            <Popper ref={popperRef} open={open} anchorEl={anchorEl} placement={placement} transition>
                {({ TransitionProps }) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <Paper className="popper_paper poper_paper_res">
                            {getSubIcons(subMenuIcons)}
                         </Paper>
                     </Fade>
                )}
            </Popper>
        </div>
    )
}

export default ShapeLibrary;
