import canvas from './Canvas'

var inputMode = {
    mode: 4,
}

inputMode.inDrawMode = function() {
    return inputMode.mode == 3 || inputMode.mode == 4;
}

inputMode.setMode = function(mode) {
    var tpanel = document.getElementById("transparentPanel");
    if (mode <= 5) {
        inputMode.mode = mode;
    }
    if (mode == 1) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'auto';
    } else if (mode == 2) {
        tpanel.style.cursor = 'all-scroll';
    } else if (mode == 3) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(false);
        tpanel.style.cursor = 'url("/pen-solid.svg") 0 20, auto';
        //tpanel.style.cursor = 'auto';
    } else if (mode == 4) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.DRAWING);
        window.Module.enableRecognition(true);
        //tpanel.style.cursor = 'url("/pen-solid.svg") 0 20, auto';
        tpanel.style.cursor = 'auto';
    } else if (mode == 5) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.ERASING);
        tpanel.style.cursor = 'url("/eraser-solid.svg"), auto';
    } else if (mode == 6) {
        window.Module.eraseAll();
        canvas.draw();
    } else if (mode == 7) {
        window.Module.inputReceiver().setMode(window.Module.InputMode.SELECTION);
        tpanel.style.cursor = 'auto';
    }
}

export default inputMode;
